	module.exports = {"pathContext":{"docHtml":"<h1 id=\"workflow-events\"><a href=\"#workflow-events\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Workflow Events</h1>\n<p><img src=\"/argo/assets/alpha.svg\" alt=\"alpha\"></p>\n<blockquote>\n<p>v2.7.2</p>\n</blockquote>\n<p>We emit Kubernetes events on certain events.</p>\n<p>Workflow state change:</p>\n<ul>\n<li><code>WorkflowRunning</code></li>\n<li><code>WorkflowSucceeded</code></li>\n<li><code>WorkflowFailed</code></li>\n<li><code>WorkflowTimedOut</code></li>\n</ul>\n<p>Node completion</p>\n<ul>\n<li><code>WorkflowNodeSucceeded</code></li>\n<li><code>WorkflowNodeFailed</code></li>\n<li><code>WorkflowNodeError</code></li>\n</ul>\n<p>The involved object is the workflow in both cases. Additionally, for node completion events, annotations indicate the name and type of the involved node:</p>\n<pre><code class=\"language-yaml\">metadata:\n  name: my-wf.160434cb3af841f8\n  namespace: my-ns\n  annotations:\n    workflows.argoproj.io/node-name: my-node\n    workflows.argoproj.io/node-type: Pod\ntype: Normal\nreason: WorkflowNodeSucceeded\nmessage: 'Succeeded node my-node: my message'\ninvolvedObject:\n  apiVersion: v1alpha1\n  kind: Workflow\n  name: my-wf\n  namespace: my-ns\n  resourceVersion: \"1234\"\n  uid: my-uid\nfirstTimestamp: \"2020-04-09T16:50:16Z\"\nlastTimestamp: \"2020-04-09T16:50:16Z\"\ncount: 1\n</code></pre>","docPath":"argo/workflow-events","proj":"argo"}}

